.ProductReviews {
    @include desktop {
        z-index: 1;
    }

    &-Wrapper {
        padding: 0;

        @include desktop {
            padding: 3.6rem 1.2rem;
        }
    }

    &-SummaryRating {
        font-size: 3rem;
    }

    &-ExpandableContentHeading,
    &-ExpandableContentButton {
        @include desktop {
            display: none;
        }
    }

    &-ExpandableContentContent {
        @include desktop {
            margin-top: 0;
        }

        &_isContentExpanded {
            padding-top: 0;
        }
    }

    & &-Button {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        height: max-content;
        margin: auto;

        @include before-desktop {
            position: relative;
            height: auto;
            width: 100%;
            margin-top: 1.2rem;
        }

        @include mobile {
            margin-top: 1.4rem;
        }
    }

    &-SummaryDetails {
        display: inline-block;
        margin-left: 1.2rem;
        font-size: 2rem;

        @include mobile {
            margin-left: 1.4rem;
        }

        span {
            font-size: 1.6rem;
            font-weight: normal;

            &::before {
                content: ' / ';
            }
        }
    }
}
