.ProductBundleItem {
    &-Wrapper {
        margin-bottom: 1.2rem;

        @include mobile {
            margin-bottom: 1.4rem;
        }
    }

    &-Title {
        font-size: 1.4rem;
        font-weight: bold;
    }

    &-Heading {
        font-weight: normal;

        &Bold {
            font-weight: bold;
        }
    }

    &-DropdownWrapper {
        display: flex;

        .Field_type_number {
            margin-left: 1.44rem;

            @include mobile {
                margin-left: 1.68rem;
            }
        }
    }

    &-Select {
        flex-grow: 1;
    }
}
